import {createStore} from 'redux';

//initial state for the store
const initial = { 
    current: 'red', 
    board: [
        [], //col 0
        [], //col 1
        [], //col 2
        [], //col 3
        [], //col 4
        [], //col 5
        [], //col 6
    ],

};

export let winnerFound = false;

function reducer(state, action){
    
    //first loop will only allow users to play if a winner has not been found
    if(winnerFound === false){ 
        
        if ( action.type === 'DROP_TILE'){
            console.log('dropping onto column ' + action.payload);
            const tile = state.current;
            
            //these 3 lines will make a new column, make a copy of the board, and combine the two
            const col = state.board[action.payload].concat(tile); //this will add the new tile to the old content in the column

            const board = state.board.slice() //we should not be able to change the board directly and need to copy it 
            board[action.payload] = col; //updating the column with the new tile

            console.log(board); 
            //will loop through the adjacent cells and identify if the person who has played wins
            WinCheck(board);
            //will check to see if the two players have tied
            TieCheck(board);

            // the state will be returned, if the last player was red, the next will be yellow and vice versa
            return{
                current: state.current === 'red' ? 'yellow' : 'red', 
                board: board,
            };
        }
        return state;
    }
    else{
        //allert is sent that the game is over
        alert("The Game is Over")
        return state
    }
    
}

function TieCheck(board){ 
   
    //iterating through the board and counting 1 to every full tile or location in the array
    let boardCounter = 0; 
    for(let y=0;y<6;y++){
        for (let x=0; x<7;x++){
            if(typeof board[x][y]!== 'undefined'){
                console.log(board[x][y]);
                boardCounter++;
            }
        }
    }

    //if after counting for every button, the count is full indicating no more spots on the board, the game will be tied
    if (boardCounter === 42){
        alert("The Game is Tied");
        winnerFound = true; 
    }
}


function WinCheck(board){

    //check for vertical winning for the user that just placed a piece
    for(let y=0; y<6; y++) { 
        for(let x=0; x<7; x++) {
            if(typeof board[x][y]!=='undefined' && typeof board[x][y+1]!=='undefined' && typeof board[x][y+2]!=='undefined' && typeof board[x][y+3]!=='undefined') {
                if((board[x][y]===board[x][y+1] && board[x][y]===board[x][y+2] && board[x][y]===board[x][y+3])){
                    alert(board[x][y]+" is the winner")
                    winnerFound = true
                }
            }
        }
    }
    
    for(let y=0; y<6; y++) {
        
        //horizontal win checker on one side of the board
        if(typeof board[0][y]!=='undefined' && typeof board[1][y]!=='undefined' && typeof board[2][y]!=='undefined' && typeof board[3][y]!=='undefined') {
            if((board[0][y]===board[1][y] && board[0][y]===board[2][y] && board[0][y]===board[3][y])){
                alert(board[0][y]+" is the winner")
                winnerFound = true
            }
        }
        //horizontal winn checker one over 
        if(typeof board[1][y]!=='undefined' && typeof board[2][y]!=='undefined' && typeof board[3][y]!=='undefined' && typeof board[4][y]!=='undefined') {
            if((board[1][y]===board[2][y] && board[1][y]===board[3][y] && board[1][y]===board[4][y])){
                alert(board[1][y]+" is the winner")
                winnerFound = true
            }
        }
        //horizontal win checker two over
        if(typeof board[2][y]!=='undefined' && typeof board[3][y]!=='undefined' && typeof board[4][y]!=='undefined' && typeof board[5][y]!=='undefined') {
            if((board[2][y]===board[3][y] && board[2][y]===board[4][y] && board[2][y]===board[5][y])){
                alert(board[2][y]+" is the winner")
                winnerFound = true
            }
        }
        //horizontal win checker 3 over
        if(typeof board[3][y]!=='undefined' && typeof board[4][y]!=='undefined' && typeof board[5][y]!=='undefined' && typeof board[6][y]!=='undefined') {
            if((board[3][y]===board[4][y] && board[3][y]===board[5][y] && board[3][y]===board[6][y])){
                alert(board[3][y]+" is the winner")
                winnerFound = true
            }
        }
    }

}

export default createStore (reducer, initial);