import React from 'react';
import './App.css';

import BoardGrid from './BoardGrid';

class App extends React.Component{

  render() { 
    const cells = []; 
    
    //creating the grid of buttons stored in rows and columns
    for( let y = 6; y >= 0; y--){
      const row = []; 

      for (let x = 0; x < 7; x++){
        // we need a unique key because each iteration will have its own x
        row.push(<BoardGrid key ={x} x={x} y={y}/> )
      }

      cells.push(<div key={y} className="row">{row}</div>)
    }
  
    return (
      <div className="App">
        {cells}
      </div>
    );
  }

}

export default App;
