import React, {Component} from 'react';
import {connect} from 'react-redux';
import {dropTile} from './actions';

class BoardButton extends Component{ 

    //click event that starts the tile drop when a click is made
    handleClick(){
        this.props.sendTileDrop(this.props.x);
        //call the method here 
    }

    render(){

      //aliasing some variables used
      const board = this.props.board; 
      const x = this.props.x; 
      const y = this.props.y;

      let classes = 'button';

      //if statements to concatenate the type of player chip will be placed down
      if(board[x][y] !== undefined){
        if (board[x][y] === 'red') { 
          classes+= ' red';
        }else{ 
          classes += ' yellow';
        }
      }
      //returning the buttons to make the board, with their coordinates displayed on them
      return(
        <button className={classes} onClick={() => this.handleClick()}>
          <p>{this.props.x}, {this.props.y}</p>
        </button>
      )
    }
  }

  const stateToProps = state => {
    return {
      board: state.board,
    };
  };

  const dispatchToProps = dispatch => { 
    return{
      sendTileDrop: col => dispatch(dropTile(col)),
    };
  };

  export default connect(stateToProps, dispatchToProps)(BoardButton); 